.contact {
  margin: 0;
  text-align: center;

  .contact-github {
    padding: 1%;

    #github {
      color: $nickel;

      :link {
        color: $nickel;
      }

      :visited {
        color: $charcoal;
      }

      :hover {
        color: $cadet-blue-crayola;
      }

      :active {
        color: $gainsboro;
      }
    }
  }

